import React from 'react'
import Link from 'gatsby-link'
import Pico8Code from '../components/pico8code';
import gif1 from "./PICO-8_0.gif";

const tweetcart1_1 = 
`function _init()
 i=0
 color=8
 t=time()
 cls()
end

function _update60()
  x=64+(32+sin(t)*7*sin(8*i+t/2))*cos(i)
  y=64+(32+sin(t)*7*sin(8*i+t/2))*sin(i)

  i+=0.01
  if i>1 then
    i=0
    t=time()
    color+=1
  end
end

function _draw()
  circfill(x,y,2,color)
  flip()
end`;

const tweetcart1 = () => (
  <div>
    <h1>Highway to hell</h1>
      <Pico8Code code={tweetcart1_1} gif={gif1}></Pico8Code>
  </div>
)

export default tweetcart1



