import React from 'react'
import PrismJS from 'prismjs'

const Pico8Code = ({code, gif}) => (
  <div>
    <img style={{
      float:"left",
      width:"512px",
      padding:"10px"
    }}
    src={gif}>
    </img>
    <div style={{
        marginleft:"256px",
      }} 
    >
      <pre class="language-lua">
        <code class="language-lua">
          {code}
        </code>
      </pre>
    </div>
  </div>
)

export default Pico8Code



